<template>
  <v-row justify="center">
    <v-dialog v-model="$store.state.fundDialogue" persistent>
      <v-card>
        <v-card-title class="headline">
          Fund Busary Application for {{ $store.state.selectedBusary.title }}
        </v-card-title>
        <v-stepper v-model="e6" vertical>
          <v-stepper-step color="black" :complete="e6 > 1" step="1">
            Terms of Donating
            <small
              >By Clicking Contninue you accept that the following steps are
              irreversible</small
            >
            <v-stepper-content step="1">
              <v-btn
                class="ma-2"
                :loading="loading"
                :disabled="loading"
                color="secondary"
                @click="e6 = 2"
              >
                Continue
              </v-btn>
            </v-stepper-content>
          </v-stepper-step>
          <v-stepper-step color="black" :complete="e6 > 2" step="2">
            Mint Some DAI
            <small
              >You need some DAI to donate or Skip if you already have a
              balance</small
            >
            <v-stepper-content step="2">
              <v-text-field
                v-model="mintAmount"
                :rules="amountRules"
                label="Amount"
                hint="Amount in DAI e.g 100 "
                outlined
              ></v-text-field>
              <v-btn
                class="ma-2"
                :loading="loading"
                :disabled="loading"
                color="secondary"
                @click="mintDAI()"
              >
                Continue
              </v-btn>
              <v-btn
                class="ma-2"
                :loading="loading"
                :disabled="loading"
                color="secondary"
                @click="e6 += 1"
              >
                Skip
              </v-btn>
            </v-stepper-content>
          </v-stepper-step>
          <v-stepper-step color="black" :complete="e6 > 3" step="3">
            Approve LiquidBusary
            <small>
              Approve LiquidBusary to stream tokens to receipient or Skip to
              Approve Infinite DAI Amount
            </small>
          </v-stepper-step>
          <v-stepper-content step="3">
            <v-btn
              class="ma-2"
              :loading="loading"
              :disabled="loading"
              color="secondary"
              @click="approve()"
            >
              Approve
            </v-btn>
          </v-stepper-content>
          <v-stepper-step color="black" :complete="e6 > 4" step="4">
            Set Amount to Stream Per Second
            <small> The default will be set if this step is skipped </small>
          </v-stepper-step>
          <v-stepper-content color="black" step="4">
            <v-text-field
              v-model="amount"
              :rules="amountRules"
              label="Amount (DAI)"
              hint="Amount in Eth e.g 1 "
              outlined
            ></v-text-field>
            <v-btn color="secondary" @click="donate()"> Donate </v-btn>
          </v-stepper-content>
        </v-stepper>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="black" text @click="$store.state.fundDialogue = false">
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
      <v-overlay :value="overlay">
        <v-progress-circular indeterminate size="90"></v-progress-circular>
      </v-overlay>
    </v-dialog>
  </v-row>
</template>

<script>
import bigNumber from "bignumber.js";
const { web3tx, toWad, toBN } = require("@decentral.ee/web3-helpers");
const { wad4human } = require("@decentral.ee/web3-helpers");
import swal from "sweetalert2";

export default {
  data() {
    return {
      overlay: false,
      mintedDAI: 100,
      e6: 1,
      loading: false,
      loader: "",
      mintAmount: 0,
      amount: 385802469135802,
      amountApprove: 0,
      amountRules: [
        (v) => !!v || "Amount is required",
        (v) =>
          (v && !isNaN(v) && Number.isInteger(v)) ||
          "Amount must be a valid integer number e.g. 1 a number with no decimals",
      ],
    };
  },
  watch: {
    loader() {},
  },
  methods: {
    approve() {
      let _this = this;

      this.overlay = true;
      this.$store.state.dai
        .approve(
          this.$store.state.daix.address,
          "115792089237316195423570985008687907853269984665640564039457584007913129639935",
          { from: this.$store.state.userAddress, gas: 6000000 }
        )
        .then(async function (i) {
          _this.amountApprove = wad4human(
            await _this.$store.state.dai.allowance.call(
              _this.$store.state.userAddress,
              _this.$store.state.daix.address
            )
          );
           _this.overlay = false;
          _this.e6++;
        })
        .catch((error) => {
          location.reload();
        });
    },
    mintDAI: async function () {
      let _this = this;

      _this.overlay = true;

      this.$store.state.dai
        .mint(
          this.$store.state.userAddress,
          this.$store.state.sf.web3.utils.toWei(
            this.mintAmount.toString(),
            "ether"
          ),
          { from: this.$store.state.userAddress, gas: 6000000 }
        )
        .then(async (results, error) => {
          _this.$store.state.userDAIBalance = wad4human(
            await this.$store.state.dai.balanceOf.call(
              this.$store.state.userAddress
            )
          );
          _this.overlay = false;
          _this.e6++;
        })
        .catch((error) => {
          location.reload();
        });

      ///call contRACT
    },
    donate() {
      let _this = this;
      _this.overlay = true;
      this.$store.state.liquidbusary.methods
        .financeBusary(this.$store.state.selectedBusary.skynetId, this.amount)
        .send({ gas: 6000000 })
        .then((reciept, error) => {
          _this.overlay = false;
          _this.success("Succesfully donated");
          _this.e6=1
          location.reload();
        })
        .catch((error) => {
          _this.error("Something went wrong whislt donating");
          location.reload();
        });
    },
    error(msg) {
      Swal.fire({
        type: "warn",
        title: "Error",
        text: msg,
      });
    },
    success(msg) {
      Swal.fire({
        type: "success",
        title: "Success",
        text: msg,
      });
    },
  },
};
</script>
