<template>
  <!-- App.vue -->

  <v-app>
    <v-app-bar absolute color="white" elevate-on-scroll app>
      <!-- dropdown menu -->

      <v-toolbar-title class="font-weight-black">LiquidBusary</v-toolbar-title>

      <v-spacer></v-spacer>
      <div class="text--primary d-flex justify-center mb-6">
        <v-chip
          class="ma-2"
          :color="$store.state.connected === true ? 'green' : 'red'"
          text-color="white"
        >
          {{ $store.state.connected.network }}
        </v-chip>
      </div>
      <div class="text--primary d-flex justify-center mb-6">
        DAI Balance: {{ $store.state.userDAIBalance }}
      </div>
      <v-spacer></v-spacer>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <!-- <v-btn text slot="activator"> -->
          <v-btn text v-on="on">
            <v-icon left> mdi-clock-time-twelve-outline </v-icon>
          </v-btn>
        </template>
        <v-list>
          <!-- v-list-tile is changed to v-list-item -->
          <v-list-item
            v-for="(account, index) in $store.state.accounts"
            :key="index"
            ><v-list-item-avatar>
              <v-img
                :src="`http://identicon-1132.appspot.com/${Math.floor(
                  Math.random() * 1000
                )}`"
              ></v-img>
            </v-list-item-avatar>
            <v-list-item-title>{{ account }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <!-- Sizes your content based upon application components -->
    <v-main>
      <!-- Provides the application the proper gutter -->
      <v-container fluid>
        <template>
          <v-card>
            <v-tabs color="black accent-4" right>
              <v-tab key="1">Apply</v-tab>
              <v-tab key="2">Pending</v-tab>
              <v-tab key="3">Funded</v-tab>
              <v-tab key="4">My Busaries</v-tab>

              <v-tab-item key="1">
                <v-container fluid>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field
                      v-model="title"
                      :rules="titleRules"
                      label="Title"
                      hint="Purpose for applying"
                      counter
                      outlined
                    ></v-text-field>
                    <v-text-field
                      v-model="amount"
                      :rules="amountRules"
                      label="Amount (Eth)"
                      hint="Amount Required"
                      outlined
                    ></v-text-field>
                    <v-textarea
                      v-model="description"
                      :rules="descriptionRules"
                      outlined
                      name="input-7-4"
                      label="Description"
                      counter
                    ></v-textarea>
                    <v-btn
                      :disabled="!valid || loading"
                      color="white"
                      :loading="loading"
                      @click="applyForBusary()"
                    >
                      Apply
                    </v-btn>
                  </v-form>
                </v-container>
              </v-tab-item>
              <v-tab-item key="2">
                <v-container fluid>
                  <v-row>
                    <v-col
                      v-for="(busary, i) in $store.state.notFunded"
                      :key="i"
                      cols="12"
                      md="4"
                    >
                      <v-card
                        class="mx-auto"
                        v-if="!busary.funded"
                        max-width="344"
                      >
                        <v-card-text>
                          <div>Busary Application for</div>
                          <p class="display-1 text--primary">
                            {{ busary.title }}
                          </p>
                          <p>Receipient</p>

                          <div class="text--primary d-flex justify-center mb-6">
                            <template>
                              <v-tooltip left>
                                <template v-slot:activator="{ on }">
                                  <v-btn
                                    v-on="on"
                                    class="mx-2"
                                    fab
                                    dark
                                    small
                                    color="black"
                                    @click="gotToEtherscan(busary.receipient)"
                                  >
                                    <v-icon dark> mdi-ethereum </v-icon>
                                  </v-btn>
                                </template>
                                View Account on Etherscan
                              </v-tooltip>
                            </template>
                          </div>
                          <p>Progress</p>
                          <div class="text--primary d-flex justify-center mb-6">
                            Raised: {{ busary.raised }} ETH
                          </div>
                          <div class="text--primary d-flex justify-center mb-6">
                            Target: {{ busary.amount }} ETH
                          </div>
                          <div
                            color="black"
                            class="text--primary d-flex justify-center mb-6"
                          >
                            <v-btn
                              class="mr-4"
                              text
                              color="black"
                              @click="
                                $store.state.busaryDetailsDialogue = true;
                                $store.state.selectedBusary = busary;
                              "
                            >
                              View Details
                            </v-btn>
                          </div>
                          <div class="text--primary d-flex justify-start mb-6">
                            <progress-bar
                              :options="options"
                              :value="
                                parseFloat(
                                  (
                                    (busary.raised / busary.amount) *
                                    100
                                  ).toFixed(2)
                                )
                              "
                            />
                          </div>
                        </v-card-text>
                        <v-card-actions
                          v-if="!busary.funded"
                          class="text--primary d-flex justify-center mb-6"
                        >
                          <v-btn
                            class="mr-4"
                            text
                            color="black"
                            :loading="loading"
                            :disabled="loading"
                            @click="withdrawFunding(busary)"
                          >
                            Withdraw Funding
                          </v-btn>
                          <v-btn
                            class="mr-4"
                            text
                            @click="fundApplicaiton(busary)"
                            color="black"
                          >
                            Fund
                          </v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-container>
              </v-tab-item>
              <v-tab-item key="3">
                <v-container fluid>
                  <v-row>
                    <v-col
                      v-for="(busary, i) in $store.state.busaries"
                      :key="i"
                      cols="12"
                      md="4"
                    >
                      <v-card
                        v-if="busary.funded"
                        class="mx-auto"
                        max-width="344"
                      >
                        <v-card-text>
                          <div>Busary Application for</div>
                          <p class="display-1 text--primary">
                            {{ busary.title }}
                          </p>
                          <p>Progress</p>
                          <div class="text--primary d-flex justify-center mb-6">
                            Raised: {{ busary.raised }} ETH
                          </div>
                          <div class="text--primary d-flex justify-center mb-6">
                            Target: {{ busary.amount }} ETH
                          </div>
                          <div class="text--primary d-flex justify-start mb-6">
                            <progress-bar
                              :options="options"
                              :value="
                                parseFloat(
                                  (
                                    (busary.raised / busary.amount) *
                                    100
                                  ).toFixed(2)
                                )
                              "
                            />
                          </div>
                          <div
                            color="black"
                            class="text--primary d-flex justify-center mb-6"
                          >
                            <v-btn
                              class="mr-4"
                              text
                              color="black"
                              @click="
                                $store.state.busaryDetailsDialogue = true;
                                $store.state.selectedBusary = busary;
                              "
                            >
                              View Details
                            </v-btn>
                          </div>
                        </v-card-text>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-container>
              </v-tab-item>
              <v-tab-item key="4">
                <v-container fluid>
                  <v-row>
                    <v-col
                      v-for="(busary, i) in $store.state.myBusaries"
                      :key="i"
                      cols="12"
                      md="4"
                    >
                      <v-card class="mx-auto" max-width="344">
                        <v-card-text>
                          <div>Busary Application for</div>
                          <p class="display-1 text--primary">
                            {{ busary.title }}
                          </p>
                          <p>Progress</p>
                          <div class="text--primary d-flex justify-center mb-6">
                            Raised: {{ busary.raised }} ETH
                          </div>
                          <div class="text--primary d-flex justify-center mb-6">
                            Target: {{ busary.amount }} ETH
                          </div>
                          <div class="text--primary d-flex justify-start mb-6">
                            <progress-bar
                              :options="options"
                              :value="
                                parseFloat(
                                  (
                                    (busary.raised / busary.amount) *
                                    100
                                  ).toFixed(2)
                                )
                              "
                            />
                          </div>
                          <div
                            color="black"
                            class="text--primary d-flex justify-center mb-6"
                          >
                            <v-btn
                              class="mr-4"
                              text
                              color="black"
                              @click="
                                $store.state.busaryDetailsDialogue = true;
                                $store.state.selectedBusary = busary;
                              "
                            >
                              View Details
                            </v-btn>
                          </div>
                        </v-card-text>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-container>
              </v-tab-item>
            </v-tabs>
          </v-card>
        </template>
        <FundView></FundView>
        <BusaryDetailsView></BusaryDetailsView>
      </v-container>
    </v-main>

    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="90"></v-progress-circular>
    </v-overlay>
    <v-footer app>
      <!-- -->
    </v-footer>
  </v-app>
</template>

<script>
import moment from "moment";
import FundView from "./views/FundView.vue";
import BusaryDetailsView from "./views/BusaryDetailsView";
import web3 from "web3";
import SuperfluidSDK from "@superfluid-finance/ethereum-contracts";
import Web3EthContract from "web3-eth-contract";
import Swal from "sweetalert2";
import { SkynetClient, genKeyPairFromSeed } from "skynet-js";
import swal from "sweetalert2";
const client = new SkynetClient();
import axios from "axios";
const Web3EthAbi = require("web3-eth-abi");
const { wad4human } = require("@decentral.ee/web3-helpers");
import bigNumber from "bignumber.js";
require("dotenv").config();
const { web3tx, toWad, toBN } = require("@decentral.ee/web3-helpers");

export default {
  components: {
    FundView,
    BusaryDetailsView,
  },
  data() {
    return {
      receipient: "",
      minDate: moment().format("YYYY-MM-DD"),
      modal: false,
      date: "",
      overlay: false,
      loading: false,
      loader: "",
      valid: true,
      title: "",
      titleRules: [
        (v) => !!v || "Busary Title is required",
        (v) =>
          (v && v.length > 2 && v.length <= 30) ||
          "Busary Title must be less than 30 characters",
      ],
      amount: 0,
      amountRules: [
        (v) => !!v || "Amount is required",
        (v) => (v && !isNaN(v)) || "Busary Amount must be a valid number",
      ],
      description: "",
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length >= 50) ||
          "Reasons for applying for busary must be atleast 50 characters",
      ],
      options: {
        text: {
          color: "#FFFFFF",
          shadowEnable: true,
          shadowColor: "#000000",
          fontSize: 14,
          fontFamily: "Helvetica",
          dynamicPosition: false,
          hideText: false,
        },
        progress: {
          color: "#2dbd2d",
          backgroundColor: "#333333",
        },
        layout: {
          height: 35,
          width: 300,
          verticalTextAlign: 61,
          horizontalTextAlign: 43,
          zeroOffset: 0,
          strokeWidth: 30,
          progressPadding: 0,
          type: "line",
        },
      },
    };
  },
  watch: {
    loader() {},
  },
  mounted: async function () {
    await this.init();
    this.checkNetwork();
  },
  methods: {
    getBusaries: async function () {
      return new Promise(async (resolve) => {
        this.$store.state.liquidbusary.methods
          .getBusaryIds()
          .call()
          .then((busaryIds) => {
            if (busaryIds.length === 0) {
              resolve();
            } else {
              var count = 0;
              busaryIds.map(async (id) => {
                var busaryDetails = this.$store.state.liquidbusary.methods
                  .getBusaryDetails(id)
                  .call({ from: this.$store.state.userAddress })
                  .then(async (busaryDetails) => {
                    count++;
                    var decoded = JSON.parse(
                      Web3EthAbi.decodeParameter("string", id)
                    );
                    var balance = await this.$store.state.sf.agreements.cfa.getNetFlow.call(
                      this.$store.state.daix.address,
                      busaryDetails[0]
                    );
             
                    this.$store.state.busaries.push({
                      description: decoded.description,
                      receipient: busaryDetails[0],
                      funded: busaryDetails[4],
                      title: decoded.title,
                      skynetId: id,
                      fundedBy: busaryDetails[1],
                      amount: busaryDetails[2],
                      raised: new bigNumber(balance)
                        .dividedBy(toWad(10))
                        .toFixed(),
                    });
                    this.$store.state.myBusaries = this.$store.state.busaries.filter(
                      (busary) =>
                        busary.receipient === this.$store.state.userAddress
                    );
                    this.$store.state.notFunded = this.$store.state.busaries.filter(
                      (busary) => !busary.funded
                    );
                    this.$store.state.busaries = this.$store.state.busaries.filter(
                      (busary) => busary.funded
                    );
              
                    if (count >= busaryIds.length) {
                      resolve();
                    }
                  })
                  .catch((error) => {
                    resolve();
                  });
              });
            }
          });
      });
    },
    init: async function () {
      this.$store.state.embarkJS.onReady(async (error) => {
        this.overlay = true;
        const sf = new SuperfluidSDK.Framework({
          chainId: 5,
          version: "0.1.2-preview-20201014",
          web3Provider: this.$store.state.provider,
        });
        await sf.initialize();
        const daiAddress = await sf.resolver.get("tokens.fDAI");
        const dai = await sf.contracts.TestToken.at(daiAddress);
        const daixWrapper = await sf.getERC20Wrapper(dai);
        const daix = await sf.contracts.ISuperToken.at(
          daixWrapper.wrapperAddress
        );
        this.$store.state.daix = daix;
        this.$store.state.dai = dai;
        this.$store.state.sf = sf;
        if (sf.chainId === 5) {
          this.$store.state.connected = true;
          this.$store.state.network = "Goerli";
        } else {
          this.$store.state.connected = false;
          this.$store.state.network = "Not Supported";
        }
        this.$store.state.userAddress = window.web3.eth.defaultAccount;
        this.$store.state.skynetClient = client;
        await this.getBusaries();
        this.$store.state.userDAIBalance = wad4human(
          await this.$store.state.dai.balanceOf.call(
            this.$store.state.userAddress
          )
        );
        this.overlay = false;
      });
    },
    error(msg) {
      Swal.fire({
        type: "warn",
        title: "Error",
        text: msg,
      });
    },
    success(msg) {
      Swal.fire({
        type: "success",
        title: "Success",
        text: msg,
      });
    },
    checkNetwork() {
      let _this = this;
      window.ethereum.on("accountsChanged", async function (accounts) {
        _this.$store.state.userAddress = accounts[0];
        location.reload();
      });
      window.ethereum.on("networkChanged", function (netId) {
        switch (netId) {
          case "1":
            this.error("Mainnet is not supported please switch to Goerli");
            return 1;
          case "2":
            this.error(
              "This is the deprecated Morden test network please switch to Goerli"
            );
            return 2;
          case "3":
            this.error(
              "This is the ropsten test network please switch to Goerli"
            );
            return 3;
          case "5":
            return 5;
          default:
            this.error("This is an unknown network please switch to Goerli");
            return -1;
        }
      });
    },
    openAccountList() {},
    applyForBusary: async function () {
      if (this.$refs.form.validate()) {
        this.overlay = true;
        let _this = this;

        var file = JSON.stringify({
          title: this.title,
          description: this.description,
          amount: this.amount,
          receipient: this.$store.state.userAddress,
        });
        let formData = new FormData();
        formData.append("file", file);
        var busaryId = Web3EthAbi.encodeParameters(["string"], [file]);
        this.$store.state.liquidbusary.methods
          .applyForBusary(busaryId, this.amount)
          .send({ gas: 6000000 })
          .then((reciept, error) => {

            _this.overlay = false;
            _this.success("Succesfully applied");
          })
          .catch((error) => {
            _this.error(
              "Something went wrong whislt uploading your application"
            );

            _this.overlay = false;
          });
      }
    },
    fundApplicaiton(busary) {
      this.$store.state.selectedBusary = busary;
      this.$store.state.fundDialogue = true;
    },
    withdrawFunding(busary) {
      let _this = this;

      this.$store.state.liquidbusary.methods
        .widthDrawFunding(busary.skynetId)
        .send({ from: this.$store.state.userAddress })
        .then((reciept) => {
          _this.success("Funding Widthdrawn");
        })
        .catch((error) => {
          _this.error("Error whilst calling withdraw funding");
        });
    },
    gotToEtherscan(address) {
      var win = window.open(
        `https://goerli.etherscan.io/address/${address}`,
        "_blank"
      );
      win.focus();
    },
  },
};
</script>
<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
